import React from 'react';
import artists from "../Data/db"
import Albums from './Albums';

const ArtistPage = (props) => {

    let arr = artists.find(el => el.id === parseInt(props.match.params.id))

    return (
        <div className="container-fluid">
            <div className='row'>
                <div className="col-md-4 col-md-offset-4 ArtistPage">
                    <div className="row">
                        <div className="col-md-6 col-xs-6 col-md-offset-3 col-xs-offset-3">
                            <img className="img-responsive" src={require(`../images/covers/${arr.cover}.jpg`)} alt="img" />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-6 col-md-offset-3 text-center">
                            <h3>{arr.name}</h3>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 text-center">
                            <p>{arr.bio}</p>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12 col-xs-12 Artist-page-col-12">
                            <div className="row">
                                {arr.albums.map((el, i) => (
                                    <Albums key={i} album={el.cover} />
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ArtistPage;

